import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Postcode from 'react-daum-postcode';
import '../postcode.scss';

const MemberComponent = ({modal, modalShowFn, deliveryModalShowFn1, deliveryModalShowFn2, deliveryModalShowFn3, service}) => {

  const onCompletePost=(data)=>{

    setField({...field, addressVal1: data.zonecode, addressVal2: data.roadAddress, isShowAddr2:false});
  }

  
  const style = {
    position: 'fixed',
    top: '50%',
    left: '50%',
    width: '400px',
    height: '500px',
    marginLeft: '-200px',
    marginTop: '-250px',
    zIndex: '3',
    background: '#fff',
    border: '1px solid #aaa'
  }

  const [field, setField] = useState({
    idVal: '',
    idCon: true,
    isShowId: false,
    isClassId1: '',
    isClassId2: '',
    idBtnClass: false,
    pwVal: '',          
    isShowPw: false,
    isClassPw1: '',
    isClassPw2: '',
    isClassPw3: '',
    pwCon:'',
    isShowPwCon: false,
    isClassPwCon: '',
    nameVal: '',
    emailVal: '',
    emailCon: true,
    emailBtnClass: false,
    hpVal: '',
    hpCon: '',          
    isDisabledHp: true,
    isShowHp: false,
    isShowCode: false,
    minutes: 2,
    seconds: 59,
    codeVal: '',
    setId: 0,
    codeConVal: '',
    codeCon: false,
    isDisabledHpInput1: false,
    isDisabledHpInput2: false,
    isDisabledHpBtn: false,
    isClassHp1: false,
    isClassHp2: false,
    isClassCode: false,
    isShowHpCount: true,
    isShowAddr: false,
    isShowAddr2: false,
    addressVal1:'',
    addressVal2:'',   
    addressVal3:'',
    addressBtnClass1: true,  
    addressBtnClass2: false,  
    genderVal: '선택안함',
    yearVal:'',
    monthVal:'',
    dateVal:'',
    isShowBirthText:'',
    isShowBirthError: false,
    addChoice: '',
    isShowAddInput: false,
    addVal: '',
    addPlaceholder: '',
    serviceChecked: [],
    serviceMust: 3,
    memberList: []
  });

  // 아이디
  const onFocusId=()=>{
    setField({...field, isShowId: true });
  }

  const onChangeId=(e)=>{
    const  regExp = /^(?=.*[A-Za-z])+(?=.*[0-9])*[^\s][A-Za-z0-9]{5,}$/g;
    let tem = '';

    if(regExp.test(e.target.value)){
      tem = true;
    }    
    else {      
      tem = false;
    }
    setField({...field, idVal: e.target.value, isClassId1: tem });
  }
  
  // 아이디 중복확인
  const onClickIdOk=(e)=>{
    e.preventDefault();

    axiosGet();

    if(field.idVal===''){
      modalShowFn('아이디를 입력해 주세요.');
    }

    else{        
      if(field.isClassId1===false){ 
        modalShowFn('6자 이상의 영문 혹은 영문과 숫자를 조합만 가능합니다.');
      }
      else {
        let tem = [];

        tem = field.memberList;
        
        let result = tem.map((item)=>item.아이디===field.idVal);
        if( result.includes(true) ) {
          modalShowFn('이미 등록된 아이디입니다.');
          setField({...field, isClassId2: false });
        }
        else{
          modalShowFn('사용 가능한 아이디입니다.');
          setField({...field, isClassId2: true , idBtnClass: true});
        }
      }
    }
  }

  // 비밀번호
  const onFocusPw=()=>{
    setField({...field, isShowPw: true});
  }

  const onChangePw=(e)=>{
    const regExp1 = /.{10,}/; 
    const regExp2 = /((?=.*[A-Za-z])+((?=.*[0-9])+|(?=.*[!@#$%&*_-])+)+)[^\s][A-Za-z0-9!@#$%&*_-]{10,}/;
    const regExp3 = /(.)\1\1/;

    let tem1 = '';
    let tem2 = '';
    let tem3 = '';

    if(regExp1.test(e.target.value)){
      tem1 = true;
    }
    else{
      tem1 = false;
    }

    if(regExp2.test(e.target.value)){
      tem2 = true;
    }
    else{
      tem2 = false;
    }

    if(regExp3.test(e.target.value)){
      tem3 = false;
    }
    else{
      tem3 = true;
    }

    setField({...field,  pwVal: e.target.value, isClassPw1: tem1, isClassPw2: tem2, isClassPw3: tem3});
  }
  
  // 비밀번호 확인
  const onFocusPwCon=()=>{
    setField({...field, isShowPwCon: true});
  }

  const onChangePwCon=(e)=>{
    let tem = '';

    if(field.pwVal === e.target.value){
      tem = true;
    }
    else{
      tem = false;
    }
    setField({...field, pwCon: e.target.value, isClassPwCon: tem});
  }

  // 이름
  const onChangeName=(e)=>{
    const regExp = /[^A-Za-z가-힣ㄱ-ㅎㅏ-ㅣ\s]/g;
    let tem = '';

    tem = e.target.value.toString().replace(regExp,'');

    setField({...field, nameVal: tem});
  }

  // 이메일
  const onChangeEmail=(e)=>{
    const regExp = /^[A-Za-z0-9]([-_.]?[A-Za-z0-9])*@[A-Za-z0-9]([-_.]?[A-Za-z0-9])*.[A-Za-z]{2,3}$/;
    let tem = '';

    if(regExp.test(e.target.value)){
      tem = true;
    }
    else{
      tem = false;
    }
    setField({...field, emailVal: e.target.value, emailCon: tem});
  }

  const onClickEmailOk=(e)=>{
    e.preventDefault();

    axiosGet();

    if(field.emailVal===''){
      modalShowFn('이메일 주소를 입력해 주세요.');
    }
    else{
      if(field.emailCon===false){
        modalShowFn('잘못된 이메일 형식입니다.');
      }
      else {

        let tem = [];

        tem = field.memberList;

        let result = tem.map((item)=>item.이메일===field.emailVal);

        if( result.includes(true) ) {
          modalShowFn('이미 등록된 이메일입니다.');
          setField({...field, emailBtnClass: false });
        }
        else{
          modalShowFn('사용 가능한 이메일입니다.');
          setField({...field, emailBtnClass: true });
        }
      }
    }
  }

  // 휴대폰
  const onChangeHp=(e)=>{
    const regExp = /^01[0|6|7|8|9]+\d{3,4}\d{4}$/g; 
    let tem = '';

    if(regExp.test(e.target.value)){
      tem = true;
    }
    else{
      tem = false;
    }

    setField({...field, hpVal: e.target.value, hpCon: tem, isDisabledHp: !tem})

  }

  const onClickHp=(e)=>{
    e.preventDefault();
    let num = Math.floor(Math.random()*900000+100000);

    if(field.hpVal===''){
      modalShowFn(`잘못된 휴대폰 번호 입니다. 확인 후 다시 시도 해 주세요.`);
    }
    else{
      modalShowFn(`인증번호(${num})가 발송되었습니다.`);
      setField({...field, isShowHp: true, codeVal: num.toString(), isDisabledHp: true, isClassHp2: true, isDisabledHpInput1: true})
    }

  }
  
  const timerCount=()=>{
    let s  = 59;
    let m = 2;
    let setId = 0;

    setId = setInterval(()=>{
      s--;
      if(s<=0){
        s=59;
        m--;
        if(m<=0){
          clearInterval(setId);
          s=0;
          m=0;
        }
      }
      setField({...field, seconds: s, minutes: m, setId: setId  });
    },1000);
  }

  useEffect(()=>{
    field.isShowHp && timerCount();
  },[field.isShowHp]);


  const onChangeHpNum=(e)=>{
      clearInterval(field.setId);
      setField({...field, codeConVal: e.target.value});
  }

  const onClickHpConfirm=(e)=>{
      e.preventDefault();
      if(field.codeVal===field.codeConVal){        
        modalShowFn(`인증이 완료되었습니다.`);
        setField({...field, isDisabledHpInput2: true, isDisabledHpBtn: true, isClassHp1: true, isClassHp2: false, codeConVal: '', isShowHpCount: false, codeCon: true, isClassCode: true, isShowCode:true});
      }
      else{
        modalShowFn(`인증번호를 확인해 주세요`);
        setField({...field, isShowCode:true, isClassCode: false});
      }
  }

  // 주소
  const onClickAddr=(e)=>{
    e.preventDefault();
    setField({...field, isShowAddr: true, isShowAddr2: true, addressBtnClass1: false, addressBtnClass2: true});
  }

  const onClickAddr2=(e)=>{
    e.preventDefault();
    setField({...field, isShowAddr: true, isShowAddr2: true});
  }

  //2. 주소입력상자1
  const  onChangeAddr1=(e)=>{    
    setField({...field, addressVal1: e.target.value }); 
  }

  //2. 주소입력상자2
  const  onChangeAddr2=(e)=>{
    setField({...field, addressVal2: e.target.value }); 
       
    let  str = field.addressVal2;
    let  deliveryTxt = e.target.value.substr(0,7);
    console.log(str, deliveryTxt);
    if(str.indexOf('서울')>=0){
      deliveryModalShowFn1(deliveryTxt);
    }
    else if(str.indexOf('경기')>=0){
      deliveryModalShowFn1(deliveryTxt);
    }
    else if(str.indexOf('제주')>=0){
      deliveryModalShowFn3();
    }
    else if(str.indexOf('울릉')>=0){
      deliveryModalShowFn3();
    }
    else if(str.indexOf('독도')>=0){
      deliveryModalShowFn3();
    }
    
  }

  const  onChangeAddr3=(e)=>{
    setField({...field, addressVal3: e.target.value }); 
  }

  // 성별
  const onChangeGender=(e)=>{
    setField({...field, genderVal: e.target.value }); 
  }

  // 생년월일
  const onChangeYear=(e)=>{
    const regExp = /[^0-9]/g;
    let tem = e.target.value.trim().replace(regExp,'');
    setField({...field, yearVal: tem}); 
  }
  const onChangeMonth=(e)=>{
    const regExp = /[^0-9]/g;
    let tem = e.target.value.trim().replace(regExp,'');
    setField({...field, monthVal: tem}); 
  }
  const onChangeDate=(e)=>{
    const regExp = /[^0-9]/g;
    let tem = e.target.value.trim().replace(regExp,'');
    setField({...field, dateVal: tem}); 
  }

  const birthDayCheck=()=>{

    const {yearVal, monthVal, dateVal} = field;
    const regExpYear  = /^(?:19[0-9][0-9]|2[0-9][0-9][0-9])$/;
    const regExpMonth = /^(?:0?[1-9]|1[0-2])$/g;
    const regExpDate  = /^(?:0?[1-9]|1[0-9]|2[0-9]|3[0-1])$/g;
    const nowYear = new Date().getFullYear();
    const nowMonth = new Date().getMonth()+1;
    const nowDate = new Date().getDate();
    const today = new Date( nowYear, nowMonth, nowDate );


    if(yearVal==='' && monthVal==='' && dateVal==='' ){
      return;
    }
    else{
      if( regExpYear.test(yearVal)===false ){
        setField({...field, isShowBirthError: true,  isShowBirthText:'태어난 년도 4자리를 정확하게 입력해 주세요.'});
        return;
      }
      else{
        setField({...field, isShowBirthError: false, isShowBirthText:''});

        if( regExpMonth.test(monthVal)===false ){
          setField({...field, isShowBirthError: true, isShowBirthText:'태어난 월을 정확하게 입력해 주세요.'});
          return;
        }
        else{
          setField({...field, isShowBirthError: false, isShowBirthText:''});

          if(regExpDate.test(dateVal)===false){ 
            setField({...field, isShowBirthError: true, isShowBirthText:'태어난 일을 정확하게 입력해 주세요.'});
            return;
          }

          else{
            setField({...field, isShowBirthError: false, isShowBirthText:''});
          }

          const birthDay  = new Date(yearVal, monthVal, dateVal);
          const nowYear120 = new Date(nowYear-120, nowMonth, nowDate);
          const nowYear14 = new Date(nowYear-14, nowMonth, nowDate);

          if( birthDay > today ){
            setField({...field, isShowBirthError: true, isShowBirthText:'생년월일이 미래로 입력되었어요.'});
            return;
          }

          else{
            setField({...field, isShowBirthError: false, isShowBirthText:''});
          }

          if( birthDay > nowYear14 ){
              setField({...field, isShowBirthError: true, isShowBirthText:'만 14세 미만은 가입이 불가 합니다.'});
              return;
          }

          else{
            setField({...field, isShowBirthError: false, isShowBirthText:''});
          }    

          if( birthDay < nowYear120 ){
            setField({...field, isShowBirthError: true, isShowBirthText:'생년월일을 다시 한 번 확인해 주세요.'});
            return;
          }

          else{
            setField({...field, isShowBirthError: false, isShowBirthText:''});
          }
        }
      }
    }
  }

  const onBlurBirth=()=>{
    birthDayCheck();
  }

  // 추가입력 사항
  const onChangeRadioAddInput=(e)=>{

    if(field.addChoice==='추천인 아이디'){
      setField({...field, isShowAddInput: true, addChoice: e.target.value, addPlaceholder: '참여 이벤트명을 입력해 주세요.' });
    }
    else {
      setField({...field, isShowAddInput: true, addChoice: e.target.value, addPlaceholder: '추천인 아이디를 입력해 주세요.' });
    }
    
  }

  const onChangeAddInput=(e)=>{
    setField({...field, addVal: e.target.value });
  }

  // 이용약관 동의
  const onChangeServiceAll=(e)=>{
    if(e.target.checked){
      setField({...field, serviceChecked: service });
    }
    else{
      setField({...field, serviceChecked: [] });
    }

  }

  const onChangeService=(e)=>{
    let tem = [];

    if(e.target.checked){

      if(e.target.value==='무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)'){
        setField({...field, serviceChecked: [...field.serviceChecked, '무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)','SNS','이메일'] });
      }
      else if(field.serviceChecked.includes('SNS') && e.target.value==='이메일'){
        setField({...field, serviceChecked: [...field.serviceChecked, '무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)','이메일'] });
      }
      else if(field.serviceChecked.includes('이메일') && e.target.value==='SNS'){
        setField({...field, serviceChecked: [...field.serviceChecked, '무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)','SNS'] });
      }
      else{
        setField({...field, serviceChecked: [...field.serviceChecked, e.target.value] });
      }

    }
    else{
        if(e.target.value==='무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)'){
          tem = field.serviceChecked.filter((item)=> item !== e.target.value);
          tem = tem.filter((item)=> item !== 'SNS');
          tem = tem.filter((item)=> item !== '이메일');      
          setField({...field, serviceChecked: tem });
        } 
        else if( field.serviceChecked.includes('SNS') && e.target.value==='이메일' ){
          tem = field.serviceChecked.filter((item)=> item !== '이메일');
          tem = tem.filter((item)=> item !== '무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)');
          setField({...field, serviceChecked: tem });
        } 
        else if( field.serviceChecked.includes('이메일') && e.target.value==='SNS' ){
          tem = field.serviceChecked.filter((item)=> item !== 'SNS');
          tem = tem.filter((item)=> item !== '무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)');
          setField({...field, serviceChecked: tem });
        } 
        else{      
          tem = field.serviceChecked.filter((item)=>item !== e.target.value );
          setField({...field, serviceChecked: tem });
        }
    }
   
  }

  // 데이터 전송 및 저장
  const onSubmitMember=(e)=>{
    e.preventDefault();
    
    const {idVal,idCon,isClassId1,pwVal,isClassPw1,isClassPw2,isClassPw3,pwCon,isClassPwCon,nameVal,emailVal,emailBtnClass,hpVal,codeCon,addressVal1,addressVal2,addressVal3,genderVal,yearVal,monthVal,dateVal,isShowBirthError,addChoice,addVal,serviceChecked} = field;

    if(idVal===''||idCon===false||isClassId1===false||pwVal===''||isClassPw1===false||isClassPw2===false||isClassPw3===false||pwCon===''||isClassPwCon===false||nameVal===''||emailVal===''||emailBtnClass===false||hpVal===''||codeCon===false||addressVal1===''||addressVal2===''||addressVal3===''||((yearVal!==''||monthVal!==''||dateVal!=='')&&isShowBirthError===true)){
      if(idVal===''){
        modalShowFn('아이디를 입력해 주세요.');
      }
      else if(idCon===false||isClassId1===false){
        modalShowFn('아이디 중복을 확인해 주세요.');
      }
      else if(pwVal===''){
        modalShowFn('비밀번호를 입력해 주세요.');
      }
      else if(isClassPw1===false||isClassPw2===false||isClassPw3===false||isClassPwCon===false){
        modalShowFn('비밀번호를 확인해 주세요.');
      }
      else if(pwCon===''){
        modalShowFn('비밀번호를 한번 더 입력해 주세요.');
      }
      else if(nameVal===''){
        modalShowFn('이름을 입력해 주세요.');
      }
      else if(emailVal===''){
        modalShowFn('이메일을 입력해 주세요.');
      }
      else if(emailBtnClass===false){
        modalShowFn('이메일 중복을 확인해 주세요.');
      }
      else if(hpVal===''){
        modalShowFn('휴대폰 번호를 입력해 주세요.');
      }
      else if(codeCon===false){
        modalShowFn('휴대폰 인증을 완료해 주세요.');
      }
      else if(addressVal1===''||addressVal2===''){
        modalShowFn('주소를 입력해 주세요.');
      }
      else if(addressVal3===''){
        modalShowFn('상세주소를 입력해 주세요.');
      }
      else if(((yearVal!==''||monthVal!==''||dateVal!=='')&&isShowBirthError===true)){
        modalShowFn('생년월일을 확인해 주세요.');
      }
      return;
    }
    else {
      let cnt=0;

      serviceChecked.map((item)=>{
        if(item.includes('필수')){
          cnt++;
        }
      });

      if(cnt<3){
        modalShowFn(`이용약관 동의 필수 선택을 체크( ${cnt} ) 해주세요.`);
        return;
      }
      else{

        let tem = {
          idData:       idVal,
          pwData:       pwVal,
          nameData:     nameVal,
          emailData:    emailVal,
          phoneData:    hpVal,
          addressData:  `${addressVal1} ${addressVal2} ${addressVal3}`,
          genderData:   genderVal,
          birthdayData: `${yearVal}-${monthVal}-${dateVal}`,
          addData :     `${addChoice}: ${addVal}`,
          serviceData:  serviceChecked,
          dateData:     `${new Date().getFullYear()}-${new Date().getMonth()+1}-${new Date().getDate()}`
        };

        localStorage.setItem(tem.idData,  JSON.stringify(tem) ); 

        let formData = new FormData();
        
        formData.append('idData',        idVal);
        formData.append('pwData',        pwVal);
        formData.append('nameData',      nameVal);
        formData.append('emailData',     emailVal);
        formData.append('phoneData',     hpVal);
        formData.append('addressData',  `${addressVal1} ${addressVal2} ${addressVal3}`);
        formData.append('genderData',    genderVal);
        formData.append('birthdayData', `${yearVal}-${monthVal}-${dateVal}`);
        formData.append('addData',      `${addChoice}: ${addVal}`);
        formData.append('serviceData',   serviceChecked);
        formData.append('dateData',     `${new Date().getFullYear()}-${new Date().getMonth()+1}-${new Date().getDate()}`);
        
        axios({
          url: './mysql/response_member.php',
          method: 'POST',
          data:  formData
        })
        .then((response)=>{
          console.log(`AXIOS 성공, ${response}`);
          modalShowFn(`${idVal} 님 회원가입이 완료되었습니다`);
          setField({  
            ...field,
            idVal: '',
            idCon: true,
            isShowId: false,
            isClassId1: '',
            isClassId2: '',
            idBtnClass: false,
            pwVal: '',          
            isShowPw: false,
            isClassPw1: '',
            isClassPw2: '',
            isClassPw3: '',
            pwCon:'',
            isShowPwCon: false,
            isClassPwCon: '',
            nameVal: '',
            emailVal: '',
            emailCon: true,
            emailBtnClass: false,
            hpVal: '',
            hpCon: '',          
            isDisabledHp: true,
            isShowHp: false,
            isShowCode: false,
            minutes: 2,
            seconds: 59,
            codeVal: '',
            setId: 0,
            codeConVal: '',
            codeCon: false,
            isDisabledHpInput1: false,
            isDisabledHpInput2: false,
            isDisabledHpBtn: false,
            isClassHp1: false,
            isClassHp2: false,
            isClassCode: false,
            isShowHpCount: true,
            isShowAddr: false,
            addressVal1:'',
            addressVal2:'',   
            addressVal3:'',
            addressBtnClass1: false,  
            addressBtnClass2: false,  
            genderVal: '선택안함',
            yearVal:'',
            monthVal:'',
            dateVal:'',
            isShowBirthText:'',
            isShowBirthError: false,
            addChoice: '',
            isShowAddInput: false,
            addVal: '',
            addPlaceholder: '',
            serviceChecked: []
          });
        })
        .catch((error)=>{
          console.log(`AXIOS 실패, ${error}`);
        });

      }
    }
  }

  const axiosGet=()=>{   
    axios({
        url:'./mysql/select.php',
        method:'GET'
    })
    .then((res)=>{
      setField({...field, memberList: res.data });
    })
    .catch((err)=>{
      console.log('axios 실패 ', err );
    });
  } 

  useEffect(()=>{
  axiosGet();
  },[]);

  return (
    <div id='member'>
      <div className="container">
          <div className="wrap">

            <div className="title"><h2>회원가입</h2></div>

            <div className="content">
             <form onSubmit={onSubmitMember} id="member" name="member" method="post" autoComplete='off'>
              <ul id="memberForm">
                <li className="page-sub"><h3><i>*</i><span>필수 입력 사항</span></h3></li>

                <li>
                  <div className="left"><label><span>아이디</span><i>*</i></label></div>

                  <div className="right">
                    <input type="text" id="inputId" name="inputId" placeholder="6자 이상의 영문 혹은 영문과 숫자를 조합" maxLength="20" onChange={onChangeId} onFocus={onFocusId} value={field.idVal} />
                    <button onClick={onClickIdOk} className={field.idBtnClass?'id-overlap-btn off' : 'id-overlap-btn'} disabled={field.idBtnClass}>중복확인</button>
                    {
                      field.isShowId && (
                        <div className="guide-text guide-id">
                          <p className={field.isClassId1=== '' ? '' : (field.isClassId1 ? 'success' : 'error')}>6자 이상의 영문 혹은 영문과 숫자를 조합</p>
                          <p className={field.isClassId2=== '' ? '' : (field.isClassId2 ? 'success' : 'error')}>아이디 중복확인</p>
                        </div>
                      )
                    }
                  </div>
                </li>

                <li>
                  <div className="left"><label><span>비밀번호</span><i>*</i></label></div>
                  <div className="right">
                    <input type="password" id="inputPw" name="inputPw" placeholder="비밀번호를 입력해주세요" maxLength="20" onChange={onChangePw} onFocus={onFocusPw} value={field.pwVal} />
                    {
                       field.isShowPw && (
                        <div className="guide-text guide-pw">
                          <p className={field.isClassPw1===''?'':(field.isClassPw1?'success':'error')}>10자 이상 입력</p>
                          <p className={field.isClassPw2===''?'':(field.isClassPw2?'success':'error')}>영문/숫자/특수문자(공백 제외)만 허용하며, 2개 이상 조합</p>
                          <p className={field.isClassPw3===''?'':(field.isClassPw3?'success':'error')}>동일한 숫자 3개 이상 연속 사용 불가</p>
                        </div>
                       )
                    }
                  </div>
                </li>

                <li>
                  <div className="left"><label><span>비밀번호확인</span><i>*</i></label></div>
                  <div className="right">
                    <input type="password" id="inputPwConfirm" name="inputPwConfirm" placeholder="비밀번호를 한번 더 입력해주세요" maxLength="20" onChange={onChangePwCon} onFocus={onFocusPwCon} value={field.pwCon} />
                    {
                      field.isShowPwCon && (
                        <div className="guide-text guide-password-confirm">
                          <p className={field.isClassPwCon===''?'':(field.isClassPwCon?'success':'error')}>동일한 비밀번호를 입력해주세요.</p>
                        </div>  
                      )
                    }           
                  </div>
                </li>

                <li>
                  <div className="left"><label><span>이름</span><i>*</i></label></div>
                  <div className="right">
                    <input type="text" id="inputName" name="inputName" placeholder="이름을 입력해주세요" maxLength="30" onChange={onChangeName} value={field.nameVal}/>
                  </div>
                </li>

                <li>
                  <div className="left"><label><span>이메일</span><i>*</i></label></div>
                  <div className="right">
                    <input disabled={field.emailBtnClass} type="email" id="inputEmail" name="inputEmail" placeholder="예: marketkurly@kurly.com" maxLength="50" onChange={onChangeEmail} value={field.emailVal} />
                    <button onClick={onClickEmailOk} className={field.emailBtnClass?'email-overlap-btn off' : 'email-overlap-btn'} disabled={field.emailBtnClass}>중복확인</button>
                  </div>
                </li>

                <li>
                  <div className="left"><label><span>휴대폰</span><i>*</i></label></div>
                  <div className="right">
                    <input disabled={field.isDisabledHpInput1} type="text" id="inputPhone" name="inputPhone" placeholder="숫자만 입력해주세요" maxLength="11" onChange={onChangeHp} value={field.hpVal}/>
                    <button onClick={onClickHp}  disabled={field.isDisabledHp} className={field.isDisabledHp?"phone-btn":"phone-btn on"}>인증번호 받기</button>
                    {
                      field.isShowHp && (
                        <div className="code-num">
                          <input disabled={field.isDisabledHpInput2} onChange={onChangeHpNum} type="text" className={field.isClassHp1?'input-code off':'input-code'} id="inputCode" name="input-code" maxLength="8" value={field.codeConVal} />
                          <button disabled={field.isDisabledHpBtn} onClick={onClickHpConfirm} className={field.isClassHp2?'cert-confirm-btn on':'cert-confirm-btn'}>인증번호 확인</button>
                          {
                            field.isShowHpCount && (
                              <p id="countdown" className="countdown">{field.minutes}:{field.seconds<10?`0${field.seconds}`:field.seconds}</p>
                            )
                          }
                          <p className="receive-info">인증번호가 오지 않는다면, 통신사 스팸 차단 서비스 혹은 휴대폰 번호 차단 여부를 확인해주세요. (마켓컬리 1644-1107)</p>
                        </div>
                      )
                    }
                    {
                      field.isShowCode && (
                        <div className="guide-text guide-receive">
                          <p className={field.isClassCode=== '' ? '' : (field.isClassCode ? 'success' : 'error')}>{field.isClassCode=== '' ? '' : (field.isClassCode ? '인증이 완료되었습니다.' : '인증번호를 확인해 주세요.')}</p>
                        </div>
                      )
                    }
                  </div>
                </li>

                <li className="address">
                  <div className="left"><label><span>주소</span><i>*</i></label></div>
                  <div className="right">
                    {
                      field.isShowAddr && ( 
                        <>
                          <input onChange={onChangeAddr1} value={field.addressVal1} type="text" id="inputAddress1" className="input-address1" name="inputAddress1" placeholder="우편 번호" readOnly/>
                          { field.addressBtnClass2 && <button onClick={onClickAddr2} className="research-btn"><span><img src="./img/ico_search.png" alt=""/>재검색</span></button>}
                          <input onChange={onChangeAddr2} value={field.addressVal2} type="text" id="inputAddress2" className="input-address2" name="inputAddress2" placeholder="주소" readOnly/>
                          <input onChange={onChangeAddr3} value={field.addressVal3} type="text" id="inputAddress3" className="input-address3" name="inputAddress3" placeholder="세부 주소를 입력하세요" />
                        </>
                      )
                    }
                    { field.addressBtnClass1 && <button onClick={onClickAddr} className="address-btn" title="주소검색"><span><img src="./img/ico_search.png" alt="" />주소검색</span></button> }
                    <div id="deliveryTxt">
                      <p className="type01">샛별배송</p>
                      <p className="type02">택배배송</p>
                      <p className="type03">배송불가</p>
                    </div>
                    
                    <p className="guide-address-text">배송지에 따라 상품 정보가 달라질 수 있습니다.</p>

                    {
                      field.isShowAddr2 && (
                        <div>
                          <Postcode
                          style={style}                           
                          onComplete={onCompletePost}
                          />
                        </div>
                      )
                    }

                  </div>
                </li>

                <li>
                  <div className="left"><label><span>성별</span></label></div>
                  <div className="right gender">
                    <label htmlFor="male"><input onChange={onChangeGender} checked={field.genderVal.includes('남자')} type="radio" id="male" name="gender" value="남자" /><span>남자</span></label>                    
                    <label htmlFor="female"><input onChange={onChangeGender} checked={field.genderVal.includes('여자')} type="radio" id="female" name="gender" value="여자" /><span>여자</span></label>                    
                    <label htmlFor="none"><input onChange={onChangeGender} checked={field.genderVal.includes('선택안함')} type="radio" id="none" name="gender" value="선택안함" /><span>선택안함</span></label>                    
                  </div>
                </li>

                <li>
                  <div className="left"><label><span>생년월일</span></label></div>
                  <div className="right">
                    <div className="date-box">
                      <ul>
                        <li><input onChange={onChangeYear} onBlur={onBlurBirth} value={field.yearVal} type="text" id="year" name="year" placeholder="YYYY" maxLength="4" /></li>
                        <li><span>/</span></li>
                        <li><input onChange={onChangeMonth} onBlur={onBlurBirth} value={field.monthVal} type="text" id="month" name="month"  placeholder="MM" maxLength='2' /></li>
                        <li><span>/</span></li>
                        <li><input onChange={onChangeDate} onBlur={onBlurBirth} value={field.dateVal} type="text" id="date" name="date"  placeholder="DD" maxLength='2' /></li>
                      </ul>
                    </div>

                    <div className="guide-txt guide-birthday-confirm">
                      {
                         field.isShowBirthError && (
                          <p>{field.isShowBirthText}</p>
                         )
                      }
                    </div>
                  </div>
                </li>

                <li>
                  <div className="left"><label><span>추가입력 사항</span></label></div>
                  <div className="right">

                    <div className="addiction">
                      <label htmlFor="recommend"><input onChange={onChangeRadioAddInput} checked={field.addChoice.includes('추천인 아이디')} type="radio" id="recommend" className="addiction-btn" name="addiction" value="추천인 아이디" /><span>추천인 아이디</span></label>                    
                      <label htmlFor="event"><input onChange={onChangeRadioAddInput} checked={field.addChoice.includes('참여 이벤트')} type="radio" id="event" className="addiction-btn" name="addiction" value="참여 이벤트명" /><span>참여 이벤트명</span></label>  
                    </div>
                    {
                      field.isShowAddInput && (
                        <div className="input-addiction-box">
                          <input onChange={onChangeAddInput} type="text" id="inputAddiction" className="input-addiction" name="input-addiction" placeholder={field.addPlaceholder} value={field.addVal}/>
                          <p className="txt-guide">추천인 아이디와 참여 이벤트 명 중 하나만 선택 가능합니다<br />가입 이후, 수정이 불가합니다.<br />대소문자 및 띄어쓰기에 유의해주세요.</p>
                        </div>
                      )
                    }
                  </div>
                </li>

                <li className="check-box">
                  <div className="left"><label><span>이용약관동의<i>*</i></span></label></div>

                  <div className="right service">

                    <ol>
                      <li>
                        <label htmlFor="chkAll"><input onChange={onChangeServiceAll} checked={field.serviceChecked.length>=7?true:false} type="checkbox" id="chkAll" name="chkAll" className="chk-all" value="전체동의합니다." /><span>전체 동의합니다.</span>
                        <p>선택항목에 동의하지 않은 경우도 회원가입 및 일반적인 서비스를 이용할 수 있습니다.</p>
                        </label>
                      </li>

                      <li className="view-box">
                        <label htmlFor="chk1"><input onChange={onChangeService} checked={field.serviceChecked.includes('이용약관 동의(필수)')} type="checkbox" id="chk1" name="chk1" className="chkbox-btn must-chk" value="이용약관 동의(필수)" /><span>이용약관 동의<i>(필수)</i></span></label>
                        <span  className="view-btn-box"><a href="#!" className="view-btn" title="약관보기">약관보기<i>&gt;</i></a></span>
                      </li>

                      <li className="view-box">
                        <label htmlFor="chk2"><input onChange={onChangeService} checked={field.serviceChecked.includes('개인정보 수집·이용 동의(필수)')} type="checkbox" id="chk2" name="chk2" className="chkbox-btn must-chk" value="개인정보 수집·이용 동의(필수)" /><span>개인정보 수집·이용 동의<i>(필수)</i></span></label>
                        <span  className="view-btn-box"><a href="#!" className="view-btn" title="약관보기">약관보기<i>&gt;</i></a></span>
                      </li>

                      <li className="view-box">
                        <label htmlFor="chk3"><input onChange={onChangeService}  checked={field.serviceChecked.includes('개인정보 수집·이용 동의(선택)')} type="checkbox" id="chk3" name="chk3" className="chkbox-btn" value="개인정보 수집·이용 동의(선택)" /><span>개인정보 수집·이용 동의<i>(선택)</i></span></label>
                        <span  className="view-btn-box"><a href="#!" className="view-btn" title="약관보기">약관보기<i>&gt;</i></a></span>
                      </li>

                      <li className="chk4-box">
                        <label htmlFor="chk4"><input onChange={onChangeService} checked={field.serviceChecked.includes('무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)')} type="checkbox" id="chk4" name="chk4" className="chkbox-btn" value="무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)" /><span>무료배송, 할인쿠폰 등 혜택/정보 수신 동의<i>(선택)</i></span></label>
                        <dl>
                            <dd>
                              <label htmlFor="chk5"><input onChange={onChangeService} checked={field.serviceChecked.includes('SNS')} type="checkbox" id="chk5" name="chk5" className="chkbox-btn" value="SNS" /><span>SNS</span></label>
                              <label htmlFor="chk6"><input onChange={onChangeService} checked={field.serviceChecked.includes('이메일')} type="checkbox" id="chk6" name="chk6" className="chkbox-btn" value="이메일" /><span>이메일</span></label>
                            </dd>
                            <dt><p>동의 시 한 달간 [5%적립] + [2만원 이상 무료배송] 첫 주문 후 안내</p></dt>
                        </dl>
                      </li>

                      <li>
                        <label htmlFor="chk7"><input onChange={onChangeService} checked={field.serviceChecked.includes('본인은 만 14세 이상입니다.(필수)')} type="checkbox" id="chk7" name="chk7" className="chkbox-btn must-chk" value="본인은 만 14세 이상입니다.(필수)" /><span>본인은 만 14세 이상입니다.<i>(필수)</i></span></label>
                      </li>

                    </ol>                  
                  </div>              
                </li>

                <li className="button-box">
                <button type="submit" className="submit-btn">가입하기</button>
                </li>

              </ul>
             </form>
            </div>

          </div>
        </div>
    </div>
  );
};

MemberComponent.defaultProps = {
  service: [
      '이용약관 동의(필수)',
      '개인정보 수집·이용 동의(필수)',
      '개인정보 수집·이용 동의(선택)',
      '무료배송, 할인쿠폰 등 혜택/정보 수신 동의(선택)',
      'SNS',
      '이메일',
      '본인은 만 14세 이상입니다.(필수)'
  ]
}

export default MemberComponent;