import React, { useState } from 'react';
import HeaderComponent from './HeaderComponent.js';
import LoginComponent from './LoginComponent.js';
import MainComponent from './MainComponent.js';
import FooterComponent from './FooterComponent.js';
import QuickComponent from './QuickComponent.js';
import ModalComponent from './ModalComponent.js';
import DeliveryModalComponent from './DeliveryModalComponent.js';
import GoToTopComponent from './GoToTopComponent.js';

const WrapComponent = () => {

  const [login, setLogin] = useState(false);
  const [member, setMember] = useState(true);

  const loginState=()=>{
    setLogin(true);
    setMember(false);
  }

  const memberState=()=>{
    setMember(true);
    setLogin(false);
  }


  const [modal, setModal] = useState({
    title: '',
    isShow: false,
    addressTitle: '',
    isShowDelivery: false,
    isShowLayer1: false,
    isShowLayer2: false,
    isShowLayer3: false
  });

  const modalShowFn=(tit)=>{
    setModal({...modal, title: tit, isShow: true});
  }
  
  const modalCloseFn=()=>{
    setModal({...modal, isShow: false});
  }

  const deliveryModalShowFn1=(addressTit)=>{
    setModal({...modal, addressTitle: addressTit, isShowDelivery: true, isShowLayer1:true});
  }

  const deliveryModalShowFn2=()=>{
    setModal({...modal, isShowDelivery: true, isShowLayer2:true});
  }

  const deliveryModalShowFn3=()=>{
    setModal({...modal, isShowDelivery: true, isShowLayer3:true});
  }

  const deliveryModalCloseFn=()=>{
    setModal({...modal, isShowDelivery: false, isShowLayer1:false, isShowLayer2:false, isShowLayer3:false});
  }

  return (
    <div id='wrap'>
      <HeaderComponent loginState={loginState} memberState={memberState}/>
      {
        login && (  
          <LoginComponent memberState={memberState} modal={modal} modalShowFn={modalShowFn}/>
        )
      }
      {
        member && (
          <MainComponent modal={modal} modalShowFn={modalShowFn} deliveryModalShowFn1={deliveryModalShowFn1} deliveryModalShowFn2={deliveryModalShowFn2} deliveryModalShowFn3={deliveryModalShowFn3} />
        )
      }
      <FooterComponent/>
      <QuickComponent/>
      <ModalComponent modal={modal} modalCloseFn={modalCloseFn} />
      <DeliveryModalComponent modal={modal} deliveryModalCloseFn={deliveryModalCloseFn}/>
      <GoToTopComponent/>
    </div>
  );
};

export default WrapComponent;