import React from 'react';

const ModalComponent = ({modal, modalCloseFn}) => {

  const onClickClose=()=>{
    modalCloseFn();
  }

  return (
    modal.isShow && (
      <div id='modal'>
        <div className="container">
          <ul>
            <li><h2>알림 메시지</h2><button onClick={onClickClose} className="modal-close colse-btn"><img src="./img/이미지77.png" alt="close" /></button></li>
            <li><p className="modal-message">{modal.title}</p></li>
          </ul>
    
          <div className="button-box"><button onClick={onClickClose} className="modal-close ok-btn">확인</button></div>
        </div>
      </div>
    )
  );
};

export default ModalComponent;