import React, { useState } from 'react';
import axios from 'axios';

const LoginComponent = ({memberState, modal, modalShowFn}) => {

  const [userId, setUserId] = useState('');
  const [userPw, setUserPw] = useState('');

  const onChangeId=(e)=>{
    setUserId(e.target.value);
  }

  const onChangePw=(e)=>{
    setUserPw(e.target.value);
  }

  const axiosEvent=()=>{
    let formData = new FormData();
    formData.append('userId', userId);
    formData.append('userPw', userPw);
    axios({
      url:'./mysql/login_cookie.php',
      method:'POST',
      data: formData
    })
    .then((res)=>{  
      console.log( res.data );

      if(res!==undefined){
        localStorage.setItem('userId', `{"아이디": "${res.data.아이디}", "이름": "${res.data.이름}"}` );
        window.location.href = './';
      }
      else {
        modalShowFn('아이디, 비밀번호를 확인해 주세요.');
      }
    })
    .catch((err)=>{
        console.log( err );
    });
  }

  const onClickLoginEvent=(e)=>{
      e.preventDefault();
      axiosEvent();
  }

  const onClickJoinEvent=()=>{
      memberState();
  }

  return (
    <div id="login">
      <div className="container">
          <div className="title">
            <h1>로그인</h1>
          </div>
          <div className="content">
            <form name='login_form' id='loginForm' method='post' autoComplete='off'>
              <ul>
                <li>
                    <input 
                    type="text" 
                    name='id' 
                    id='id' 
                    onChange={onChangeId}
                    value={userId}
                    placeholder='아이디를 입력해주세요' 
                    />
                </li>
                <li>
                <input 
                    type="password" 
                    name='pw' 
                    id='pw' 
                    onChange={onChangePw}
                    value={userPw}
                    placeholder='비밀번호를 입력해주세요' 
                    />                        
                </li>
                <li>
                  <div class='search'>
                      <span><a href="#!">아이디 찾기</a></span>
                      <span><i>|</i></span>
                      <span><a href="#!">비밀번호 찾기</a></span>
                  </div>
                </li>
                <li>
                  <button onClick={onClickLoginEvent} class='login-btn' type='button'>로그인</button>
                </li>
                <li>
                  <button onClick={onClickJoinEvent} class='gaib-btn' type='button'>회원가입</button>
                </li>
              </ul>
            </form>
          </div>
      </div>
    </div>
  );
};

export default LoginComponent;