import React from 'react';

const DeliveryModalComponent = ({modal, deliveryModalCloseFn}) => {

  const onClickClose=()=>{
    deliveryModalCloseFn();
  }

  return (
    modal.isShowDelivery && (
      <div id='deliveryModal'>
        <div className="layer-container">

          {
            modal.isShowLayer1 && (
              <div className="layer layer-star">
                <ul>
                  <li><strong className="address-idx">{modal.addressTit}</strong> 해당주소는<br />샛별배송 지역입니다.</li>
                  <li className="img-gap"><img src="./img/img_delivery_kurly.png" alt="" /></li>
                  <li className="layer-txt">매일 아침, 문 앞까지 신선함을 전해드려요.</li>
                </ul>
                <div className="layer-btn-box">
                  <button onClick={onClickClose} className="layer-close-btn">확인</button>
                </div>
              </div>
            )
          }

          {
            modal.isShowLayer2 && (
              <div className="layer layer-nomal">
                <ul>
                  <li>택배배송 지역입니다.</li>
                  <li className="img-gap"><img src="./img/img_delivery_kurly_nomal.png" alt="" /></li>
                  <li className="layer-txt">오늘 주문하면 다음 날 바로 도착해요</li>
                  <li className="layer-txt2">일요일은 배송 휴무로 토요일에는 주문 불가</li>
                </ul>
                <div className="layer-btn-box">
                  <button onClick={onClickClose} className="layer-close-btn">확인</button>
                </div>
              </div>
            )
          }

          {
            modal.isShowLayer3 && (
              <div className="layer layer-none">
                <ul>
                  <li>배송 불가 지역입니다.</li>
                  <li className="img-gap"><img src="./img/img_delivery_none.png" alt="" /></li>
                  <li className="layer-txt">더 많은 곳에 배송될 수 있게 최선을 다하겠습니다.</li>
                </ul>
                <div className="layer-btn-box">
                  <button onClick={onClickClose} className="layer-close-btn">확인</button>
                </div>
              </div>
            )
          }

        </div>
      </div>
    )
    
  );
};

export default DeliveryModalComponent;