import React, { useState, useEffect } from 'react';

const HeaderComponent = ({loginState,memberState}) => {
  
  const [isShow, setIsShow] = useState ({isShowMenu: false,isShowMobile: ''});

  const onMouseOverMenu=()=>{
    setIsShow({...isShow, isShowMenu: true});
  }

  const onMouseLeaveMenu=()=>{
    setIsShow({...isShow, isShowMenu: false});
  }

  const onFocusmenu=()=>{
    setIsShow({...isShow, isShowMobile: 'flex'});
  }

  const [isLogin, setIsLogin] = useState(false);
  const [userName, setUserName] = useState('');
  const [popup, setPopup] = useState(false);

  const onClickLogout=(e)=>{
      e.preventDefault();
      let value = getCookie('userId');
      let newDate = new Date();
      newDate.setDate(newDate.getDate()-1);            
      document.cookie = `${'userId'}=${value}; path=/; expires=${newDate.toUTCString()};`;
      localStorage.removeItem('userId');
      setIsLogin(false);
      setUserName('');
  }

  const loginEvent=()=>{
    if( localStorage.length <= 0 ) return;
    const localData = JSON.parse(localStorage.getItem('userId')); 
    let result = getCookie('userId');
    if( result === localData.아이디 ){
      setIsLogin(true);
      setUserName(localData.이름 + '님');
  }
  }        
  
  useEffect(()=>{
    loginEvent();
  },[]);

  const onClickMember=(e)=>{
    e.preventDefault();
    memberState();
  }

  const onClickLogin=(e)=>{
    e.preventDefault();
    loginState();
  }

  const getCookie=(name)=>{
    let temp = [];
    let obj = [];
    let found = '';

    if(document.cookie==='') return;

    temp = document.cookie.split(';');

    temp.map((item, idx)=>{
      obj[idx] = {
         name:  item.split('=')[0].trim(),
         value: item.split('=')[1].trim()
      } 
    });  

    obj.map((item)=>{
      if(item.name === name){
         found = item.value;
      }
    });

    return found;
  }
  
  const onClickPopupClose=(e)=>{
    e.preventDefault();
    setPopup(false);

    function setCookie(name, value, expires){

      let newDate = new Date();

      newDate.setDate( newDate.getDate()+expires );
      document.cookie = `${name}=${value}; path=/; expires=${newDate.toUTCString()};`;
    }

    setCookie('popup_event', 'no', 1);

  }

  const popupOpen=()=>{

    const result = getCookie('popup_event');

    if(result === 'no'){
      setPopup(false);
    }
    else{
      setPopup(true);
    }
    
  }                

  useEffect(()=>{
    popupOpen();          
  },[]);

  return (
    <header id="header">
      <div id="container">
        <div className="wrap">
          
          {
            popup && (
              <div className="popup">
                <div className="popup-container">
                  <a href="#!" title='이벤트'><span>지금 가입하고 인기상품 <p>100원</p>에 받아가세요!<i><img src="./img/이미지01.png" alt="" /></i></span></a>
                  <span><button onClick={onClickPopupClose} className="popup-close-btn" title="닫기"><img src="./img/이미지02.png" alt="" /></button></span>
                </div>
              </div>
            )
          }

          <div className="user-menu">

            <div className="user-list">
              <a href="#!" title='배송안내'><img src="./img/이미지04.png" alt="" /></a>
              <ul className="list-sub">
                <li className="list-link link1">
                  {
                    isLogin === false ? <a href="#!" onClick={onClickMember} title="회원가입">회원가입</a> : <a href="#!" title='웰컴'>웰컴</a>                          
                  }
                </li>
                <li className="list-link link2">
                  {
                    isLogin === false ? <a href="#!" onClick={onClickLogin} title="로그인">로그인</a> : <a href="#!">{userName}님!</a>
                  }
                </li>
                <li className="list-link link3">
                  {
                    isLogin === false ? <a href="#!" title='고객센터'>고객센터<img src="./img/ico_down_8x5.webp" alt="" /></a> : <a href="#!" onClick={onClickLogout} title="로그아웃">로그아웃</a>
                  }
                </li>
              </ul>
            </div>

            <h1><a href="#!"className="logo" title="logo"><img src="./img/이미지03.png" alt="logo" /></a></h1>
          </div>

          <div className="fixed-container">
            <div id="mainMenu">
              <div className="gnb-box">
                <a onFocus={onFocusmenu} href="#!" title='메인 메뉴' className='mobile-btn'><i className="icon"></i></a>
                
                <ul className="gnb" style={{display: `${isShow.isShowMobile}`}}>
                  <li className="category"><a onMouseOver={onMouseOverMenu} href="#!" title='전체 카테고리'><i className="icon"></i><span className="category-txt">전체 카테고리</span></a>
                  {
                    isShow.isShowMenu && (
                      <div id="menuContainer" onMouseLeave={onMouseLeaveMenu}>
                        <ol className="menu-view">
                          <li className="menu-list list1"><a href="#!" title='KURLY BRITH WEEK'><i></i><p className="list-name">KURLY BRITH WEEK</p></a></li>
                          <li className="menu-list list2"><a href="#!" title='채소'><i></i><p className="list-name">채소</p></a></li>
                          <li className="menu-list list3"><a href="#!" title='과일·견과·쌀'><i></i><p className="list-name">과일·견과·쌀</p></a></li>
                          <li className="menu-list list4"><a href="#!" title='수산·해산·건어물'><i></i><p className="list-name">수산·해산·건어물</p></a></li>
                          <li className="menu-list list5"><a href="#!" title='정육·계란'><i></i><p className="list-name">정육·계란</p></a></li>
                          <li className="menu-list list6"><a href="#!" title='국·반찬·메인요리'><i></i><p className="list-name">국·반찬·메인요리</p></a></li>
                          <li className="menu-list list7"><a href="#!" title='샐러드·간편식'><i></i><p className="list-name">샐러드·간편식</p></a></li>
                          <li className="menu-list list8"><a href="#!" title='면·양념·오일'><i></i><p className="list-name">면·양념·오일</p></a></li>
                          <li className="menu-list list9"><a href="#!" title='생수·음료·우유·커피'><i></i><p className="list-name">생수·음료·우유·커피</p></a></li>
                          <li className="menu-list list10"><a href="#!" title='간식·과자·떡'><i></i><p className="list-name">간식·과자·떡</p></a></li>
                          <li className="menu-list list11"><a href="#!" title='베이커리·치즈·델리'><i></i><p className="list-name">베이커리·치즈·델리</p></a></li>
                          <li className="menu-list list12"><a href="#!" title='건강식품'><i></i><p className="list-name">건강식품</p></a></li>
                          <li className="menu-list list13"><a href="#!" title='와인'><i></i><p className="list-name">와인</p></a></li>
                          <li className="menu-list list14"><a href="#!" title='전통주'><i></i><p className="list-name">전통주</p></a></li>
                          <li className="menu-list list15"><a href="#!" title='생활용품·리빙·캠핑'><i></i><p className="list-name">생활용품·리빙·캠핑</p></a></li>
                          <li className="menu-list list16"><a href="#!" title='스킨케어·메이크업'><i></i><p className="list-name">스킨케어·메이크업</p></a></li>
                          <li className="menu-list list17"><a href="#!" title='헤어·바디·구강'><i></i><p className="list-name">헤어·바디·구강</p></a></li>
                          <li className="menu-list list18"><a href="#!" title='주방용품'><i></i><p className="list-name">주방용품</p></a></li>
                          <li className="menu-list list19"><a href="#!" title='가전제품'><i></i><p className="list-name">가전제품</p></a></li>
                          <li className="menu-list list20"><a href="#!" title='반려동물'><i></i><p className="list-name">반려동물</p></a></li>
                          <li className="menu-list list21"><a href="#!" title='베이비·키즈·완구'><i></i><p className="list-name">베이비·키즈·완구</p></a></li>
                          <li className="menu-list list22"><a href="#!" title='컬리의 추천'><i></i><p className="list-name">컬리의 추천</p></a></li>
                        </ol>
                      </div>
                    )
                  }
                  </li>    

                  <li className="menu menu1"><a href="#!" title='신상품'><span className="txt">신상품</span></a></li>
                  <li className="menu menu2"><a href="#!" title='베스트'><span className="txt">베스트</span></a></li>
                  <li className="menu menu3"><a href="#!" title='알뜰쇼핑'><span className="txt">알뜰쇼핑</span></a></li>
                  <li className="menu menu4"><a href="#!" title='특가/혜택'><span className="txt">특가/혜택</span></a></li>
                </ul>
              </div>
 
              <div id="sideSearch" className="gnb-search">
                <form action="search.php"><input type="text" aria-label="검색어" placeholder="검색어를 입력해주세요." /><button><img src="./img/ico_search_x2.webp" alt="" /></button></form>
              </div>
 
              <div className="icons">
                <div className="location"><a href="#!" title='배송지 등록'><img src="./img/이미지10.svg" alt="" /></a></div>
                <div className="add-cart"><a href="#!" title='마이페이지'><img src="./img/이미지12.svg" alt="" /></a></div>
                <div className="cart-count"><a href="#!" title='장바구니'><img src="./img/이미지07.svg" alt="" /></a></div>
              </div>
  
            </div>
          </div>

        </div>
      </div> 
    </header>
  );
};

export default HeaderComponent;